.hero_main{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-content: center;
    width: 100%;
    height: 100%;
}

.hero_section{
    display: flex;
    justify-content: center;
    /* align-items: center; */
    width: 80%;
    height: 80%; 
    
}

.hero_p{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hero_heading{
    /* display: flex; */
}
.hero_heading > h1{
    font-size: 50px;
    background: linear-gradient(90deg, rgb(126, 6, 129) 0%, rgb(53, 119, 204) 45%, rgba(0,212,255,1) 100%); 
    -webkit-text-stroke: 1px rgb(17, 7, 66);
    background-clip: text;
    color: transparent; 
}

.hero_p > p{
    color: white;
}

.hero_img{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 500px;
    background: rgba( 255, 255, 255, 0.35 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 12.5px );
    -webkit-backdrop-filter: blur( 12.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.hero_img > img{
    width: 90%;
    border-radius: 10px;
}

